<template>
    <div>
        <video ref="video" autoplay playsinline style="display:none"></video>
        <div>
            <canvas id="output"></canvas>
        </div>
        <div>
            <button @click="startRecognition">辨識</button>
        </div>
    </div>
</template>

<script>
    import { markRaw } from 'vue';
    import { ref, onMounted } from 'vue';
    import * as faceLandmarksDetection from '@tensorflow-models/face-landmarks-detection'
    import * as tf from '@tensorflow/tfjs-core'
    import '@mediapipe/face_mesh'
    import '@tensorflow/tfjs-backend-webgl'
    import * as THREE from 'three'
    import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
    import { TRIANGULATION } from '@/assets/triangulation.js';
    import { UV_COORDS } from '@/assets/uv_coords.js';

    export default {
        name: 'FaceRecognitionComponent',
        setup() {
            const video = ref(null);
            const model = ref(null);

            const setupCamera = async () => {
                const stream = await navigator.mediaDevices.getUserMedia({ video: true });
                video.value.srcObject = stream;

                return new Promise((resolve) => {
                    video.value.onloadedmetadata = () => {
                        resolve(video.value);
                    };
                });
            };
            let dModel = null;
            const MAX_FACES = 2;
            const createModel = async () => {
                await tf.setBackend('webgl');
                model.value = markRaw(await faceLandmarksDetection.createDetector(
                    faceLandmarksDetection.SupportedModels.MediaPipeFaceMesh, {
                    maxFaces: MAX_FACES,
                    refineLandmarks: true,
                    runtime: 'mediapipe',
                    solutionPath: 'https://cdn.jsdelivr.net/npm/@mediapipe/face_mesh',
                    minDetectionConfidence: 0.5,
                    minTrackingConfidence: 0.5
                }
                ));
            };

            const recognition = async () => {
                try {
                    const faces = await model.value.estimateFaces(video.value, { flipHorizontal: false });


                    for (let index = 0; index < MAX_FACES; index++) {
                        if (faces.length > index) {
                            const keypoints = faces[index].keypoints;
                            var list = keypoints.reduce((acc, pos) => {
                                acc.push([pos.x, pos.y, pos.z]);
                                return acc;
                            }, []);

                            render3D({
                                scaledMesh: list,
                            }, index);
                        } else {
                            render3D({ scaledMesh: [] }, index);
                        }
                    }


                } catch (error) {
                    console.log('识别错误:', error);
                }
            };



            const startRecognition = async () => {
                await recognition();
                setInterval(recognition, 100);
            };

            const render3D = (data, index) => {
                renderer.render(scene, camera)
                updateGeometry(data, index);
                if (index == 0)
                    track(dModel, data)
            };

            const scene = new THREE.Scene()
            const geometrys = [];
            let camera = null;
            let renderer = null;
            const initThree = () => {
                const vw = video.value.videoWidth
                const vh = video.value.videoHeight


                const backgroundTexture = new THREE.VideoTexture(video.value)
                const background = new THREE.Mesh(
                    new THREE.PlaneGeometry(vw, vh),
                    new THREE.MeshBasicMaterial({
                        map: backgroundTexture
                    })
                )
                background.position.set(0, 0, -1000)
                scene.add(background)



                camera = new THREE.OrthographicCamera(
                    vw / -2, vw / 2, vh / 2, vh / -2, 0.1, 2000
                );

                camera.position.set(0, 0, 1000)
                camera.lookAt(scene.position)





                const hemiLight = new THREE.HemisphereLight(0xffffff, 0xffffff, 0.3)
                scene.add(hemiLight)
                const ambientLight = new THREE.AmbientLight(0xffffff, 0.7)
                scene.add(ambientLight)
                const directionalLight = new THREE.DirectionalLight(0xffffff, 0.7)
                directionalLight.position.set(0.5, 0, 0.866)
                scene.add(directionalLight);

                renderer = markRaw(new THREE.WebGLRenderer({
                    canvas: document.getElementById("output"),
                    alpha: true,
                    antialias: true
                }))
                renderer.setPixelRatio(window.devicePixelRatio)
                let w = window.innerWidth - 16;
                let h = window.innerHeight - 130;
                const videoAspectRatio = vw / vh;
                if (w / h > videoAspectRatio) {
                    w = h * videoAspectRatio;
                } else {
                    h = w / videoAspectRatio;
                }
                renderer.setSize(w, h)

                for (var i = 0; i < MAX_FACES; i++) {
                    const geometry = new THREE.BufferGeometry();
                    geometry.setIndex(TRIANGULATION)
                    geometry.setAttribute('uv', new THREE.Float32BufferAttribute(UV_COORDS.map((item, index) => index % 2 ? item : 1 - item), 2))
                    geometry.computeVertexNormals();

                    const textureLoader = new THREE.TextureLoader();
                    const meshImg = "https://image-static.segmentfault.com/296/909/2969096738-6274dd64ee615_fix732";

                    textureLoader.load(meshImg, texture => {
                        texture.encoding = THREE.sRGBEncoding
                        texture.anisotropy = 16
                        const material = new THREE.MeshBasicMaterial({
                            map: texture,
                            transparent: true,
                            color: new THREE.Color(0xffffff),
                            reflectivity: 0.5
                        });
                        const mesh = new THREE.Mesh(geometry, material)
                        scene.add(mesh)
                    })
                    geometrys.push(geometry)
                }
                renderer.render(scene, camera)
            };


            const getScale = (coords, id0 = 0, id1 = 1) => {
                const v1 = new THREE.Vector3(...coords[id0])
                const v2 = new THREE.Vector3(...coords[id1])
                return v1.distanceTo(v2)
            }

            const getRotation = (coords, top, left, right) => {
                const p0 = new THREE.Vector3(...coords[top])
                const p1 = new THREE.Vector3(...coords[left])
                const p2 = new THREE.Vector3(...coords[right])
                const matrix = new THREE.Matrix4()
                const x = p1.clone().sub(p2).normalize()
                const y = p1.clone().add(p2).multiplyScalar(0.5).sub(p0).multiplyScalar(-1).normalize()
                const z = new THREE.Vector3().crossVectors(x, y).normalize()
                matrix.makeBasis(x, y, z)
                return matrix.invert()
            }

            const load3DModel = async () => {

                const loader = new GLTFLoader();
                dModel = new THREE.Object3D();
                dModel.position.set(0, 0, 0)
                await loader.load('https://caiwenlie.github.io/ARFaceEffects/models/animal_head/rabit.glb', (gltf) => {
                    const object = gltf.scene
                    const box = new THREE.Box3().setFromObject(object)
                    //const size = box.getSize(new THREE.Vector3()).length()
                    const center = box.getCenter(new THREE.Vector3())
                    object.position.x += (object.position.x - center.x);
                    object.position.y += (object.position.y - center.y + 1);
                    object.position.z += (object.position.z - center.z - 15);
                    dModel.add(object)
                    scene.add(dModel)
                })
            };

            const track = (object, prediction) => {
                //const annotations = prediction.annotations
                //const position = annotations.midwayBetweenEyes[0]

                const box = new THREE.Box3().setFromObject(object);
                const size = new THREE.Vector3();
                box.getSize(size);

                const position = prediction.midwayBetweenEyes;
                const scaleX = getScale(prediction.scaledMesh, 234, 454)
                const scaleY = getScale(prediction.scaledMesh, 10, 152)
                const rotation = getRotation(prediction.scaledMesh, 10, 50, 280)
                position[1] += scaleY / 10;
                object.position.set(...position)
                object.scale.set(scaleX / 20, scaleY / 25, scaleX / 20);
                object.scale.x *= -1
                object.rotation.setFromRotationMatrix(rotation)
                object.rotation.y = -object.rotation.y
                object.rotateZ(Math.PI)
            }


            const updateGeometry = (prediction, index) => {
                const geometry = geometrys[index];
                const vw = video.value.videoWidth
                const vh = video.value.videoHeight
                const faceMesh = resolveMesh(prediction.scaledMesh, vw, vh)
                const positionBuffer = faceMesh.reduce((acc, pos) => acc.concat(pos), [])
                prediction.midwayBetweenEyes = faceMesh[10];
                //geometry.setAttribute('position', new THREE.Float32BufferAttribute(positionBuffer, 3))
                //geometry.attributes.position.needsUpdate = true

            }

            const resolveMesh = (faceMesh, vw, vh) => {
                return faceMesh.map(p => [p[0] - vw / 2, vh / 2 - p[1], -p[2]])
            }

            onMounted(async () => {
                await setupCamera();
                await createModel();
                await load3DModel();
                initThree();
            });


            return {
                video,
                startRecognition,
            };
        },
    };
</script>

<style scoped>

    video {
        width: 100%;
        height: auto;
    }
</style>
